* {
  box-sizing: border-box;
  list-style: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  outline: none; }

.button {
  border-radius: 10px;
  height: 25px;
  background: #cddc39;
  font-family: "Comfortaa";
  padding: 0 20px;
  line-height: 25px;
  font-size: 0.875rem;
  cursor: pointer;
  color: #ffffff;
  border: none; }

.text_roboto {
  font-family: "Roboto-Regular"; }

.text_comfortaa {
  font-family: "Comfortaa"; }

.d-none {
  display: none; }

.d-block {
  display: block; }

body {
  font-family: "Roboto", "Comfortaa", sans-serif;
  position: relative; }

.header {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 30px; }

.header__head {
  margin: 0 auto; }

.header__logo {
  display: block; }

.header__img {
  display: block;
  width: 302px;
  height: auto;
  margin-bottom: 47px; }

.header__favourites {
  position: absolute;
  right: 10px;
  top: 0;
  display: flex;
  align-items: center; }

.header__svg {
  width: 14px;
  height: 14px;
  fill: #673ab7; }

.header__favourite {
  color: #673ab7;
  font-size: 1rem;
  margin-left: 12px; }

.header__search {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px; }

.header__input {
  border-radius: 10px;
  border: 1px solid #6d6e71;
  height: 25px;
  color: #6d6e71;
  font-size: 0.625rem;
  line-height: 0.625rem;
  padding: 0 15px;
  width: 300px;
  margin-bottom: 10px; }

@media (min-width: 1170px) {
  .header__favourites {
    top: 33px;
    right: 32px; }
  .header__favourites_null {
    top: -97px; }
  .header__search {
    flex-direction: row; }
  .header__input {
    width: 450px;
    margin-bottom: 0;
    margin-right: 15px; }
  .header_null {
    transform: translate(0, 130px); } }

.main__section {
  width: 240px;
  margin: 0 auto; }

.main__favorite {
  font-size: 1rem;
  color: #673ab7;
  font-weight: normal;
  margin-bottom: 20px; }

.main__gallery {
  width: 210px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-bottom: 30px; }

.main__item {
  height: 122px;
  width: 210px;
  border: 1px solid #6d6e71;
  background: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 13px; }

.main__img {
  max-width: 210px;
  max-height: 122px;
  cursor: pointer; }

.main__button {
  display: block;
  margin: 0 auto; }

.popup__blur {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 999; }

.popup__img {
  width: auto;
  height: 150px;
  margin-bottom: 15px; }

.popup__nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 114px;
  height: 34px;
  border-radius: 10px;
  background: #673ab7; }

.popup__svg {
  cursor: pointer; }

.popup__svg_back, .popup__svg_next {
  height: 10px;
  width: 9px;
  fill: #ffffff; }

.popup__svg_star {
  height: 11px;
  width: 11px;
  fill: #cddc39; }

.popup__svg_close {
  width: 10px;
  height: 10px;
  fill: #ffffff; }

.main__item_favorite {
  position: relative; }

.gallery__svg_delete {
  display: block;
  fill: #ffffff;
  width: 12px;
  height: 12px;
  position: absolute;
  right: 7px;
  top: 7px;
  z-index: 998;
  cursor: pointer; }

@media (min-width: 1170px) {
  .main__section {
    width: 900px; }
  .main__gallery {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%; }
  .popup__img {
    width: auto;
    height: 408px;
    margin-bottom: 15px; }
  .main__item {
    margin-right: 15px; } }
